;(function(){
  'use strict';

  angular
    .module('heatMapModule')
    .controller('heatMapController', heatMapController);


  heatMapController.$inject = [
    '$mdDialog',
    '$scope',
    'foursquareAPIFactory',
    'modalFactory',
    'geolocationFactory',
    'leafletFactory',
    'gmapsFactory'
  ];

  function heatMapController($mdDialog, $scope, foursquareAPIFactory, modalFactory, geolocationFactory, leafletFactory, gmapsFactory){
    /* jshint ignore:start */
    var vm = this;
    /* jshint ignore:end */

    vm.close = _close;
    vm.filter = _filter;


    /**
     *
     * @private
     */
    function _close() {
      $mdDialog.cancel();
    }

    /**
     *
     * @private
     */
    function _filter() {

      var cords = {};

      gmapsFactory.getAddress(vm.city).then(function(data){

        cords = {
          latitude: data.data.results[0].geometry.location.lat,
          longitude: data.data.results[0].geometry.location.lng
        };

        leafletFactory.start(cords.latitude, cords.longitude);

        foursquareAPIFactory.searchVenues(cords, 1000000, '').then(function(data){
          _showHeatMapPlaces(data, cords);
        });
      });
    }

    /**
     *
     * @param places
     * @param cords
     * @private
     */
    function _showHeatMapPlaces(places, cords) {
      _close();
      leafletFactory.generateHeatMap(places.data.response.venues, cords);
    }

  }
})();

;(function(){
  'use strict';

  angular
    .module('app')
    .factory('modalFactory', modalFactory);

  modalFactory.$inject = ['$mdDialog'];

  function modalFactory($mdDialog) {
    var baseTemplateUrl = 'modules/';
    var templateUrl, controller;

    return {
      showPlacesModal: _showPlacesModal,
      showWelcomeModal: _showWelcomeModal,
      loadingModal: _loadingModal,
      showTopFiveModal: _showTopFiveModal,
      showHeatMapModal: _showHeatMapModal
    };

    /////////////////

    /**
     *
     * @param ev
     * @param templateUrl
     * @param controller
     * @returns {*}
       * @private
       */
    function _open(templateUrl, controller) {
      return $mdDialog.show({
        controller: controller,
        templateUrl: templateUrl,
        parent: angular.element(document.body),
        clickOutsideToClose:true
      });
    }


      /**
       *
       * @param ev
       * @returns {*}
       */
    function _showPlacesModal() {
      templateUrl = baseTemplateUrl + 'places/places.template.html';
      controller = 'PlacesController as vm';
      return  _open(templateUrl, controller );
    }

    /**
     *
     * @param ev
     * @returns {*}
     */
    function _showWelcomeModal() {
      templateUrl = baseTemplateUrl + 'welcome/welcome.template.html';
      controller = 'welcomeController as vm';
      return  _open(templateUrl, controller );
    }

    /**
     *
     * @param ev
     * @param open
     * @returns {*}
     * @private
     */
    function _loadingModal(open) {
      templateUrl = 'common/layout/modals/loading.template.html';

      if(open)
        return  _open(templateUrl, '');
      else
        $mdDialog.cancel();
    }

    /**
     *
     * @returns {*}
     * @private
     */
    function _showTopFiveModal() {
      templateUrl = baseTemplateUrl + 'topFive/topFive.template.html';
      controller = 'topFiveController as vm';
      return  _open(templateUrl, controller );
    }

    /**
     *
     * @returns {*}
     * @private
     */
    function _showHeatMapModal() {
      templateUrl = baseTemplateUrl + 'heatMap/heatMap.template.html';
      controller = 'heatMapController as vm';
      return  _open(templateUrl, controller );
    }

  }

})();

;(function(){
  'use strict';

  angular
    .module('app')
    .factory('leafletFactory', leafletFactory);

  leafletFactory.$inject = [
    'MAPBOX_CLIENTID',
    'MAPBOX_ACCESTOKEN',
    'geolocationFactory'
  ];

  function leafletFactory(MAPBOX_CLIENTID, MAPBOX_ACCESTOKEN, geolocationFactory){
    var map;


    var markerIcon = L.icon({
      iconUrl: 'assets/img/marker.svg'
    });

    var currentLocationIcon = L.icon({
      iconUrl: 'assets/img/userMarker.svg'
    });

    return {
      start: _start,
      setView: _setView,
      setMarker: _setMarker,
      setMarkerWithPopup: _setMarkerWithPopup,
      generateHeatMap: _generateHeatMap
    };

    /////////////////

    /**
     *
     * @param lat
     * @param long
     * @private
     */
    function _start(lat, long){
      if(map !== undefined)
        map.remove();

      map = L.map('map').setView([lat, long], 13);
      _startImagery();
      _setMarker(lat, long);
    }

    /**
     *
     * @private
     */
    function _startImagery(){
      L.tileLayer('https://api.tiles.mapbox.com/v4/{id}/{z}/{x}/{y}.png?access_token=' + MAPBOX_ACCESTOKEN, {
        attribution: 'Map data &copy; <a href="http://openstreetmap.org">OpenStreetMap</a> contributors, <a href="http://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery Â© <a href="http://mapbox.com">Mapbox</a>',
        maxZoom: 18,
        id: MAPBOX_CLIENTID,
        accessToken: MAPBOX_ACCESTOKEN
      }).addTo(map);
    }

      /**
       *
       * @param lat
       * @param long
       * @private
       */
    function _setView(lat, long) {
      map.setView(L.latLng(lat, long), 16);
      _setMarker(lat, long);
    }

    /**
     *
     * @param lat
     * @param long
     */
    function _setMarker(lat, long) {
      L.marker(L.latLng(lat, long), {icon: currentLocationIcon})
        .bindPopup('Current location! =)')
        .addTo(map);
    }

    /**
     *
     * @param lat
     * @param long
     * @param popupText
     * @private
     */

    function _setMarkerWithPopup(lat, long, popupText) {
      L.marker(L.latLng(lat, long), {icon: markerIcon})
        .bindPopup(popupText)
        .addTo(map);
    }

    /**
     *
     * @param places
     * @param cords
     * @private
     */
    function _generateHeatMap(places, cords){

      var heatMapMarkers = [];

      angular.forEach(places, function(v, k) {
        heatMapMarkers.push([
          places[k].location.lat,
          places[k].location.lng
        ]);
      });

      var heat = L.heatLayer(heatMapMarkers,{
        radius: 20,
        blur: 15,
        maxZoom: 17,
      }).addTo(map);

      _setView(cords.latitude, cords.longitude);
    }

  }

})();

;(function(){
  'use strict';

  angular
    .module('app')
    .factory('gmapsFactory', gmapsFactory);

  gmapsFactory.$inject = [
    '$http'
  ];

  function gmapsFactory($http){


    return {
      getAddress: _getAddress
    };

    /////////////////


    function _getAddress(address){
      return $http.get('http://maps.googleapis.com/maps/api/geocode/json?address='+ address +'&sensor=false');
    }

  }

})();

;(function(){
  'use strict';

  angular
    .module('app')
    .factory('geolocationFactory', geolocationFactory);

  geolocationFactory.$inject = [
    '$q',
    '$mdDialog'
  ];

  function geolocationFactory($q, $mdDialog) {
    var cords = {};
    var deferred = $q.defer();

    return {
      getPermission: _getPermission,
      getLocation: _getLocation
    };

    ///////////////

    /**
     *
     * @private
     */
    function _getPermission() {

      if (navigator.geolocation) {
        navigator.geolocation.watchPosition(_setLocation);
      }
      else {
        deferred.reject("Geolocation is not supported by this browser.");
      }

      return deferred.promise;
    }


    /**
     *
     * @param data
     * @private
     */
    function _setLocation(data) {
      cords = {
        latitude: data.coords.latitude,
        longitude: data.coords.longitude
      };

      deferred.resolve(cords);
    }

    /**
     *
     * @returns {{}}
     * @private
     */
    function _getLocation() {
      return cords;
    }

  }
})();

;(function(){
  'use strict';

  angular
    .module('app')
    .factory('foursquareAPIFactory', foursquareAPIFactory);

  foursquareAPIFactory.$inject  = [
    'FOURSQUARE_CLIENID',
    'FOURSQUARE_CLIENSECRET',
    'FOURSQUARE_APIVERSION',
    'FOURSQUARE_APIURL',
    'FOURSQUARE_VERSIONPARAMETER',
    '$http',
    '$httpParamSerializerJQLike'
  ];

  function foursquareAPIFactory(FOURSQUARE_CLIENID, FOURSQUARE_CLIENSECRET, FOURSQUARE_APIVERSION, FOURSQUARE_APIURL, FOURSQUARE_VERSIONPARAMETER, $http, $httpParamSerializerJQLike) {
    var base_url = FOURSQUARE_APIURL + FOURSQUARE_APIVERSION;

    return {
      getCategories: _getCategories,
      searchVenues: _searchVenues,
      searchTrendings: _searchTrendings
    };

    ///////////////

    /**
     *
     * @param endpoint
     * @param params
     * @returns {*}
     * @private
     */
    function _get(endpoint, params) {
      var defaultParams = {
        client_id: FOURSQUARE_CLIENID,
        client_secret: FOURSQUARE_CLIENSECRET,
        v: FOURSQUARE_VERSIONPARAMETER
      };

      if(params !== null) {
        defaultParams = angular.merge(defaultParams, params);
      }

      var paramsURL = $httpParamSerializerJQLike(defaultParams);

      return $http.get(base_url + endpoint + paramsURL);
    }
    /**
     *
     * @returns {*}
     * @private
     */
    function _getCategories() {
      var endpoint = 'venues/categories?';

      return _get(endpoint, null);
    }

    /**
     *
     * @param cords
     * @param radius
     * @param categories
     * @returns {*}
     * @private
     */
    function _searchVenues(cords, radius, categories) {
      var endpoint = 'venues/search?';
      var ids = [];

      angular.forEach(categories, function(v, k) {
        if(categories[k].selected)
          ids.push(categories[k].id);
      });

      if(ids.length > 1)
        ids.join(',');
      else
        ids = ids[0];

      var params = {
        ll: cords.latitude + ',' + cords.longitude,
        radius: radius,
        categoryId: ids
      };

      return _get(endpoint, params);
    }

    /**
     *
     * @param cords
     * @param radus
     * @param limit
     * @returns {*}
     * @private
     */
    function _searchTrendings(cords, radius, limit) {
      var endpoint = 'venues/trending?';

      var params = {
        ll: cords.latitude + ',' + cords.longitude,
        radius: radius,
        limit: limit
      };

      return _get(endpoint, params);
    }

  }
})();

